/* Button */
._2hTXI {
  position: relative;
  overflow: hidden;
  cursor: pointer;
  text-align: center;
  
  margin: 1em;
  font-size: 1.5em;
  text-shadow: -1px -1px 0 #333, 1px -1px 0 #333, -1px 1px 0 #333, 1px 1px 0 #333;
  color: #ffd1dc; 

  &._2m7FH{
    padding: 1.7em 0.5em;
  }

  &._3moFT{
    padding: 0.5em;
  }
}

/* Sakura Animation */
._3EG4o {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('sakura-image.jpg') no-repeat center center;
  background-size: cover;
}

._2hTXI:hover._SJqzZ ._3EG4o {
  background: url('sakura-animation-360p.gif') no-repeat center center;
  background-size: cover;
}

._2hTXI:hover._1Y5Hg ._3EG4o {
  background: url('sakura-animation-480p.gif') no-repeat center center;
  background-size: cover;
}

._2hTXI:hover._1Vk37 ._3EG4o {
  background: url('sakura-animation-720p.gif') no-repeat center center;
  background-size: cover;
}

/* Default Button */
._3UK6o {
  border: 2px solid #333;
  border-radius: 0.5em;
  font-size: 1.5em;
  text-shadow: -1px -1px 0 #333, 1px -1px 0 #333, -1px 1px 0 #333, 1px 1px 0 #333;
}

/* Primary Button */
._3ljpl {
  border: 2px solid #333;
  border-radius: 0.5em;
  color: #69ffff; 
}

/* Dashed Button */
._27xYC {
  border: 2px solid #333;
  border-radius: 0.5em;
  border-style: dashed;
}

/* Text Button */
._5bXm4 {
  background-color: transparent;
  border: none;

  ._3EG4o{
    background: none !important;
  }
}

/* Link Button */
._71S8l {
  background-color: transparent;
  border: none;
  color: #69ffff;
  text-decoration: underline;

  ._3EG4o{
    background: none !important;
  }
}
